<template>
  <el-container>
    <el-header>Docker Compute Admin</el-header>
    <el-main>
      <el-tabs type="card" @tab-click="handleClick">
        <el-tab-pane label="Container List" name="container">
          <el-table :data="ontainerList" border style="width: 100%">
            <el-table-column prop="Id" label="id" width="280">
            </el-table-column>
            <el-table-column prop="Names" label="names" width="180">
            </el-table-column>
            <el-table-column prop="Image" label="image">
            </el-table-column>
            <el-table-column prop="Status" label="status">
            </el-table-column>
            <el-table-column prop="Created" label="created">
            </el-table-column>
            <el-table-column prop="action" label="action">
              <template slot-scope="scope">
                <el-button type="primary" @click="restartContainer(scope.row.Id)">restart</el-button>
                <el-button type="danger" @click="stopContainer(scope.row.Id)">stop</el-button>
                <el-button type="danger" @click="removeContainer(scope.row.Id)">remove</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="Image List" name="image">
          <el-table :data="imageList" border style="width: 100%">
            <el-table-column prop="Id" label="Id" width="280">
            </el-table-column>
            <el-table-column prop="RepoTags" label="RepoTags" width="180">
            </el-table-column>
            <el-table-column prop="Created" label="Created">
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
import dayjs from 'dayjs'
export default {
  data() {
    return {
      ontainerList: [],
      imageList: []
    }
  },
  mounted: function () {
    this.queryContainerList();
    this.queryImageList();
  },
  methods: {//方法事件处理
    queryContainerList() {
      const that = this
      axios.get('http://localhost:8080/container').then(res => {
        this.ontainerList = res.data.map(s => {
          s.Created = dayjs(s.Created * 1000).format('YYYY-MM-DD HH:mm:ss')
          return s
        })
      })
    },
    queryImageList() {
      const that = this
      axios.get('http://localhost:8080/image').then(res => {
        this.imageList = res.data.map(s => {
          s.Created = dayjs(s.Created * 1000).format('YYYY-MM-DD HH:mm:ss')
          return s
        })
      })
    },
    stopContainer(id) {
      console.log(id)
    },
    restartContainer(id) {
      const that = this
      axios.post(`http://localhost:8080/container/restart/${id}`).then(res => {
        console.log(res.data)
        that.queryContainerList();
      })
    },
    removeContainer(id) {
      const that = this
      axios.post(`http://localhost:8080/container/remove/${id}`).then(res => {
        that.queryContainerList();
      })
    }
  }
}
</script>


